import React, { Component } from "react";
import MetaHead from "../../components/MetaHead";
import WebHeadForWorkPage from "../../components/WebHeadForWorkPage";
import { data } from "../data.json";
import Label from "../../components/Label";
import { GH_PAGE_URL } from "../../constants";
import FacebookComment from "../../components/FacebookComment";
import dynamic from "next/dynamic";
import {
  Layout,
  ArticleSubTitle,
  ArticleTitle,
  Author,
  MainPhoto,
  DateAndLabels
} from "./work-css";
import Sidebar from "../../components/Sidebar";

const getId = data => {
  console.log(data);
  return;
};

const DynamicWorkingComponent = dynamic(() =>
  import(`../../components/Work/youtube-video-api`)
);

class Work extends Component {
  state = {
    showSidebar: false
  };

  static async getInitialProps(context) {
    const { id } = context.query;
    const articleData = data.find(articleData => articleData.id === id);
    return { articleData };
  }

  componentDidMount = () => {
    // getId(this.props.articleData);
    window.addEventListener("scroll", this.handleScroll);
  };

  componentWillUnmount = () => {
    window.removeEventListener("scroll", this.handleScroll);
  };

  handleScroll = () => {
    const element = this.refs.element;
    const windowScrollPosition = window.pageYOffset;
    // 35為多計算margin推出去的隔間空白色區塊
    if (windowScrollPosition > element.offsetTop - 35) {
      this.setState({
        showSidebar: true
      });
    } else {
      this.setState({
        showSidebar: false
      });
    }
  };

  render() {
    const {
      title,
      subtitle,
      author,
      date,
      labels,
      url
    } = this.props.articleData;
    const href = `${GH_PAGE_URL}/work/${url}`;

    return (
      <div>
        <MetaHead
          title={`${title} ${subtitle} | Ernie Working Note 爾尼工作筆記`}
        />
        <WebHeadForWorkPage />
        <Layout>
          <ArticleTitle>{title}</ArticleTitle>
          <ArticleSubTitle>
            {subtitle}
            <iframe
              src={`https://www.facebook.com/plugins/share_button.php?href=${href}&layout=button_count&size=small&width=92&height=20&appId`}
              width="92"
              height="20"
              style={{
                border: "none",
                overflow: "hidden",
                marginLeft: "5px"
              }}
              scrolling="no"
              frameBorder="0"
              allowtransparency="true"
              allow="encrypted-media"
            />
          </ArticleSubTitle>
          <Author>
            <div className="author-image" />
            <div className="author-name-and-labels">
              <p className="author-name">{author}</p>
              <DateAndLabels>
                <span className="date">{date}</span>
                {labels.map((label, i) => (
                  <Label
                    key={i}
                    name={label.labelName}
                    type={label.labelCode}
                  />
                ))}
              </DateAndLabels>
            </div>
          </Author>
        </Layout>
        <MainPhoto />
        <Layout ref="element">
          <Sidebar
            showSidebar={this.state.showSidebar}
            articleData={this.props.articleData}
          />
          <DynamicWorkingComponent />
          <FacebookComment href={href} />
        </Layout>
      </div>
    );
  }
}

export default Work;
